.old-footer {
  display: none;
  width: 100%;
  bottom: 0px;
  background-color: rgba(0, 0, 0, 0.87);
  color: white;
  height: 130px;
  margin: 0px;
}

.footer {
  z-index: 10;

  .footer-container {
    max-width: 100vw;
    bottom: 0;
    background: rgba(0, 0, 0, 0.87);
    color: white;
    z-index: 11;
    // @media screen and (max-width: 550px) {

    // }

    h2 {
      margin: 20px 0;
    }

    .clean-list {
      li {
        p {
          span {
            font-weight: bold;
            color: white;
          }
        }
      }
      max-width: 800px;
      list-style-type: none;
      margin: 0;
      padding: 0;

      > * {
        margin-top: 10px;
        text-decoration: none !important;
      }
    }
    // .footer-top {
    //     // background-color: rgb(78, 78, 78);
    // }
    .footer-middle {
      // padding: 50px;

      a,
      p {
        color: rgb(136, 136, 136);
        margin: 10px;
      }
      .content {
        width: 100%;
        // max-width: 900px;
      }
      .social-share {
        margin-top: 20px;
        display: flex;
        justify-content: space-evenly;
      }
    }
    .footer-bottom {
      box-shadow: 0 -1px 15px 0px #00000057;

      color: rgb(134, 134, 134) !important;
      width: 100%;
      min-height: 60px;

      .terms-privacy > * {
        margin: 0 30px;
      }
    }
  }
  button {
    background-color: rgba(255, 255, 255, 0);
    border: none;
    font-size: rem(18px);
    text-align: left;
    border-radius: 3px;
    cursor: pointer;
    height: 50px;
    padding-inline-start: 10px;
    outline: none;
    color: white;
  }
}
.content {
  display: flex;
  justify-content: space-evenly;
  align-items: baseline;
}

.social-btn {
  display: flex;
  justify-content: space-evenly;
}

.share-icons-container {
  display: flex;
  justify-content: center;
  
}

@media screen and (max-width: 780px) {
  .content {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
  }
}
